import { defineComponent } from "@vue/runtime-core";
import TimerVue from "./Timer.vue";
var objects = [
    {
        id: 1,
        timesec: 10,
        message: "ciao"
    },
    {
        id: 2,
        timesec: 10,
        message: "hello"
    }
];
export default defineComponent({
    components: {
        TimerVue: TimerVue
    },
    data: function () {
        return {
            items: objects
        };
    },
    mounted: function () {
    },
    methods: {
        refresh: function (index) {
            this.items[index].timesec = 10;
        }
    }
});
